.gpt3__whatgpt3 {
    display:  flex;
    flex-direction: column;
    padding: 2rem;
    background: var(--color-footer);
    background:-moz-radial-gradient(circle at 3% 25%, rgba(49, 170, 202, 1) 0%, rgba(6, 5, 35, 1) 100%);
    background:-webkit-radial-gradient(circle at 3% 25%, rgba(49, 170, 202, 1) 0%, rgba(6, 5, 35, 1) 100%);
    background:-o-radial-gradient(circle at 3% 25%, rgba(49, 170, 202, 1) 0%, rgba(6, 5, 35, 1) 100%);
    background:radial-gradient(circle at 3% 25%, rgba(49, 170, 202, 1) 0%, rgba(6, 5, 35, 1) 100%);
}
.gpt3__whatgpt3-features{
    display: flex;
}
.gpt3__whatgpt3-features .gpt3__whatgpt3-container__feature{
    margin: 0;
}
.gpt3__whatgpt3-features .gpt3__features-container__feature-text{
    max-width: 700px;
}
.gpt3__whatgpt3-heading{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.gpt3__whatgpt3-heading h1{
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    max-width: 510px;
    font-family: var(--font-familiy);
}
.gpt3__whatgpt3-heading p{
    font-family: var(--font-familiy);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-subtext);
    cursor: pointer;
}
.gpt3__whatgpt3-container{
    flex-wrap: wrap;
    display: flex;
    flex-direction: row;
    margin-top: 2rem;
}


@media screen and (max-width: 850px) {
    .gpt3__whatgpt3-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .gpt3__whatgpt3-heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {
    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-features .gpt3__features-container__feature {
        flex-direction: column;
    }

    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-features .gpt3__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .gpt3__whatgpt3-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-container .gpt3__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}